@import url(https://fonts.googleapis.com/css2?family=Niconne&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.spellbook{
    position: relative !important;
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 0 1% 0 1%;
    max-width: 100%;
    max-height: 100%;
    margin: 0 auto;
    margin-top: -2%;  
}
.spellbook > img {
    display: flex;
    width: auto;
    padding: 0 1% 0 1%;
    max-width: 100%;
    margin: 0 auto;
    -webkit-filter: contrast(85%) brightness(83%) saturate(130%);
            filter: contrast(85%) brightness(83%) saturate(130%);  
}
.chapter {
    position: absolute !important;
    top: 5%;
    left: 14%;
    width: 35%;
    height: 80%;
}
.card {
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: absolute !important;
    top: 31%;
    right: 17%;
    background-color: transparent !important;
    border: none !important;
    width: 25%;
    height: 55%;
    z-index: 1;

}

.card-img-bottom {
        object-fit: contain;
        max-width: 90%;
        -webkit-filter: contrast(75%) brightness(83%) saturate(60%);
                filter: contrast(75%) brightness(83%) saturate(60%);
        width: 90% !important;
        height: 100%;
        margin: auto;
        position: absolute;
        bottom: auto;
        right: 8%;
        z-index: 2;
        padding: 4% 0;
}
.adhesive {
    background-image: url(/LuvengardeChronicles/static/media/ducttape.f2fdb928.png);
    background-size: 90% 100%;
    background-repeat: no-repeat;
    margin: auto;
    position: absolute !important;
    top: 28.5%;
    right: 13.5%;
    background-color: transparent !important;
    border: none !important;
    width: 30%;
    height: 61%;
    z-index: 999;

}

.containeru {
    background-image: url(/LuvengardeChronicles/static/media/background.e9900a21.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100vw;
    
    
}

.chroniclesTitle {
    text-align: center;
    margin-bottom: 0;
    font-family: 'Niconne', cursive;

}

