.component-wrapper {
    transition: height 0.5s;
    overflow: hidden;
  }
   
  .before-enter {
    opacity: 0;
  }
   
  .entering {
    opacity: 1;
    transition: opacity 0.5s;
  }
   
  .before-leave {
    opacity: 1;
  }
   
  .leaving {
    opacity: 0;
    transition: opacity 0.5s;
  }

  .sixMonths{
      position: absolute;
      bottom: 6.5%;
      right: 17%;
      font-family: 'Niconne', cursive;
  }
